.node {
    width: 1.5vw;
    height: 1.5vw;
    outline: 1px solid rgba(227, 170, 238, 0.75);
    display: inline-block;
}

.node-finish {
    background-color: red;
}

.node-start {
    background-color: green;
}
.node-visited {
  animation-name: visitedAnimation;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes visitedAnimation {
  0% {
    transform: scale(1.3);
    background-color: rgba(81, 247, 75, 0.925);
    /* border-radius: 100% */
  }

  10% {
    transform: scale(0.3);
    background-color: rgba(66, 0, 55, 0.75);
    border-radius: 100%;
  }

  50% {
    background-color: rgba(159, 11, 189, 0.493);
  }

  75% {
    transform: scale(1.2);
    background-color: rgba(71, 233, 144, 0.842);
  }
  
  100% {
    transform: scale(1);
    background-color: rgba(187, 52, 214, 0.75);
  }
}

.node-shortest-path {
  animation-name: shortestPath;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes shortestPath {
  0% {
    transform: scale(0.6);
    background-color: rgb(226, 226, 32);
  }

  50% {
    transform: scale(1.2);
    background-color: rgb(11, 221, 39);
  }

  100% {
    transform: scale(1);
    background-color: rgb(2, 240, 34);
    outline: 1px solid rgb(112, 252, 131);
  }
}
.node-wall{
  background-color: rgb(12, 53, 71);
  outline: 1px solid rgb(12, 53, 71);
  animation-name: nodeWall;
  animation-duration: 0.3s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

